import React from 'react';
import { Link } from 'react-router-dom';

const CardItems = (probs) => {
    return (
        <>
            <li className="inside_card">
                 <Link className = "inside_card_link" to={probs.path}>
                       <figure className ="inside_card_figure" data-category={probs.label}>
                                <img className ="inside_card_image" src = {probs.src} alt = "Travel Image"/>
                       </figure>
                       <div className = "inside_card_info">
                              <h5 className = "inside_card_text">{probs.text}</h5>
                       </div>
                 </Link>
            </li>
        </>
    )
}

export default CardItems
